import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-cart-order-summary',
  templateUrl: './cart-order-summary.component.html',
  styleUrls: ['./cart-order-summary.component.scss']
})
export class CartOrderSummaryComponent implements OnInit {

  showItems: boolean = false
  showItemSummary:boolean = false

  constructor() { }

  ngOnInit() {
  }

  getShowItems(value){
    this.showItems = value
    this.showItemSummary = value
  }

}



