import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CartComponent } from './cart.component';
import { CartDetailComponent } from './cart-detail/cart-detail.component';
import { CartOrderComponent } from './cart-order/cart-order.component';
import { CartOrderSummaryComponent } from './cart-order-summary/cart-order-summary.component';

const cartRoutes: Routes = [
  {
    path: '', component: CartComponent,
    children: [
      {
        path: '', component: CartDetailComponent,
        data: {
          topbar: {
            urlBackLink: '/order-product',
            internal: false,
            headerText: 'สั่งซื้อสินค้า',
            backlink : true
          }
        }
      },
      {
        path: 'cart-order', component: CartOrderComponent,
        data: {
          topbar: {
            urlBackLink: '/cart',
            internal: false,
            headerText: 'สั่งซื้อสินค้า',
            backlink : true
          }
        }
      },
      {
        path: 'cart-order-summary', component: CartOrderSummaryComponent,
        data: {
          topbar: {
            urlBackLink: '/cart/cart-order',
            internal: false,
            headerText: 'สั่งซื้อสินค้า',
            backlink : true
          }
        }
      },
    ]
  }
];


@NgModule({
  imports: [RouterModule.forChild(cartRoutes)],
  exports: [RouterModule],
  providers: []
})
export class CartRoutingModule { }



