import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-order-summary-information',
  templateUrl: './order-summary-information.component.html',
  styleUrls: ['./order-summary-information.component.scss']
})
export class OrderSummaryInformationComponent implements OnInit {

  active: boolean = true

  shItems: boolean = false

  @Output() showItems = new EventEmitter<boolean>();

  constructor() {
  }

  public readmore(): void {
    this.active = !this.active;
    this.shItems = !this.shItems;
    this.showItems.emit(this.shItems);
  }

  ngOnInit() {
  }
}



