import { Component, OnInit, Input } from "@angular/core";

@Component({
  selector: "app-cart-trade-items",
  templateUrl: "./cart-trade-items.component.html",
  styleUrls: ["./cart-trade-items.component.scss"]
})
export class CartTradeItemsComponent implements OnInit {
  @Input() editMode: boolean;
  public active: boolean = true;

  public item = {
    stepQty: 1,
    quotaFlg: "Y",
    qty: 5,
    stockQty: 10
  };
  public qtyTemp = 5; //should copy form item.qty
  public btnCalc: boolean = false;
  public btnConfirmOrder: boolean = false;


  constructor() {}

  ngOnInit() {}

  onIncrement() {
    if (this.item.quotaFlg == "N") {
      this.item.qty = this.item.qty + this.item.stepQty;
    } else if (this.item.quotaFlg == "Y") {
      if (this.item.qty == this.item.stockQty) {
      } else {
        this.item.qty = this.item.qty + 1;
      }
    }
    if (this.item.qty == this.qtyTemp) {
      this.btnCalc = false;
    } else {
      this.btnCalc = true;
    }
    this.btnConfirmOrder = false;
  }

  onDecrement() {
    if (this.item.qty == 0) {
    } else {
      this.item.qty = this.item.qty - this.item.stepQty;
    }
    if (this.item.qty == this.qtyTemp) {
      this.btnCalc = false;
    } else {
      this.btnCalc = true;
    }
    this.btnConfirmOrder = false;
  }

  deletePromotion() {
    // this.editMode = false;
    console.log("delete!")
  }

  clearQty() {
    this.item.qty = 0;
    if (this.item.qty == this.qtyTemp) {
      this.btnCalc = false;
    } else {
      this.btnCalc = true;
    }
  }

  calcPromotion() {
    this.btnConfirmOrder = true;
    this.btnCalc = false;
  }

  confirmOrder() {
    this.qtyTemp = this.item.qty;
    this.btnConfirmOrder = false;
  }

  readmore() {
    this.active = !this.active
  }

}



