import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-payment-and-ship',
  templateUrl: './payment-and-ship.component.html',
  styleUrls: ['./payment-and-ship.component.scss']
})
export class PaymentAndShipComponent implements OnInit {
  public shReceiver: boolean = false;
  public cpReceiver: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  showReceiver(){
    this.shReceiver = !this.shReceiver;
    if(this.shReceiver){
      this.cpReceiver = false
    }
  }

  companyReceiver() {
    this.cpReceiver = !this.cpReceiver;
    if(this.cpReceiver){
      this.shReceiver = false
    }
  }
}



